<template>
	<div class="home-page" >
		<div class="home-page-hot">
			<div class="home-page-hot-list">
			    <h1 class="list-title">正在热映({{hotList.length}}部)
			        <router-link tag="div" class="list-hot-item" to="/hot-more">
				       	<span class="hot-more-link"><img src="../../common/images/go.png"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			    </h1>
			    <ul style="width: 100%;height:300px;">
			        <li v-for="item in hotList" 
			            	class="hot-item" 
			            	style="width:25%;float: left;margin-bottom:20px;text-align:center;"
			            	>
			            	<div class="hot-move" >
			                	<a 
			                		:href="'http://m.mtime.cn/#!/movie/'+item.id+'/'" 								style="position: relative; width: 100%;"
			                	>
			                		<div class="score" 								style="position:absolute;top:-5px;right:4px;width: 20px;background:#659d0e;color:#fff;font-size: 12px; line-height: 16px;"
			                			v-html="item.r>0?item.r:''"
			                			></div>
			                		<img v-lazy="item.img" 
			                		     style="width:5.8rem;height:9rem;"	
			                		></a>
			                	<div class="text" 
			                		style="margin:0 auto;width: 90%;height: 28px; overflow: hidden;font-size: 14px;"
			                	>
			                		<span class="name" v-html="item.t" ></span>
			              		</div>
			              </div>
			            </li>
			        </ul>
			        <div class="loading-container"
			        	 v-show="!hotList.length"
			        	 style="position: absolute;width: 100%;top: 50%;transform:translateY(-50%);"
			        	 >
        				<loading></loading>
      				</div>
			    </div>
		        <div class="about-to-show">
		        	<h1 class="list-aboutToShow-title">即将上映({{aboutToShow.length}}部)
			    	<router-link tag="div" class="list-aboutToShow-item" to="/aboutToShow-more">
				       	<span class="aboutToShow-more-link"><img src="../../common/images/go.png"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
		          </h1>
		        </div>
			</div>
			<div class="line"></div>
			<div class="hotspot">
				<h1 class="list-hotspot-title">今日热点</h1>
				<ul class="hotSpot-list">
		            <li v-for="item in hotSpot" class="hotSpot-item">
		            	<div class="hotSpot-item-list">
		            		<div class="hotSpot-item-list-pic">
		            			<img :src="item.img" class="img"/>
		            		</div>
		            		<div class="hotSpot-item-list-txt">
		            			<h2 class="title">{{item.title}}</h2>
		            			<p class="text">{{item.desc}}</p>
		            		</div>
		            	</div>
		            </li>
		        </ul>
			</div>
			<div class="tab">
			    <router-link tag="div" class="tab-item" to="/home-page">
			        <span class="tab-link">首页</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/ticket">
			      <span class="tab-link">购票</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/shop">
			      <span class="tab-link">商店</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/search">
			      <span class="tab-link">搜索</span>
			    </router-link>
			    <router-link tag="div" class="tab-item" to="/login">
			      <span class="tab-link">我的</span>
			    </router-link>
		  	</div>
		  	<div class="copying">
		  		<p>
		  			<span>Copyright </span>
		  			<span> HiMovies.com Inc. All rights reserved.</span>
		  		</p>
		  	</div>
		</scroll>
	</div>
</template>
<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
    export default {
    	data(){
    		return{
    			hotList:[],
    			aboutToShow:[],
    			hotSpot:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getHot();
	      	this._getAboutToShow();
	      	this._getHotSpot();
	    },
	    methods:{
	      	_getHot(){
	      		axios.get("http://120.78.84.152:8080/api/getHot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotList = res.data;
	      		})
	      	},
	      	_getAboutToShow(){
	      		axios.get("http://120.78.84.152:8080/api/getAboutToShow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.aboutToShow = res.data;
	      		})
	      	},
	      	_getHotSpot(){
	      		axios.get("http://120.78.84.152:8080/api/getHotSpot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotSpot = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus" >
.home-page
	position:relative
	top:88px
	.home-page-hot
		padding:6px 18px
		.home-page-hot-list
			height: 340px
			overflow:hidden
			border-bottom:1px solid #d8d8d8
			position:relative
			.list-title
				width: 100%
				height: 40px
				font:bolder 18px/40px "微软雅黑"
				position:relative
				.list-hot-item
					position:absolute
					top:0px
					right:5px
					.hot-more-link img
						margin-top:10px
						width: 20px;
						height: 20px;
		.about-to-show .list-aboutToShow-title
			width: 100%
			height: 40px
			font:bolder 18px/40px '微软雅黑'
			position:relative
			.list-aboutToShow-item
				position:absolute
				top:0px
				right:5px
				.aboutToShow-more-link img
					margin-top:10px
					width: 20px;
					height: 20px;
	.line
		width: 100%
		height: 10px
		background:#f6f6f6	
		
	.hotspot	
		padding:6px 18px	
		.list-hotspot-title		
			width: 100%
			height: 40px
			font:bolder 16px/40px '微软雅黑'
		.hotSpot-list
			width:100%	
			.hotSpot-item
				margin-top:15px
				width: 100%
				height: 90px
				padding-bottom:20px
				border-bottom:1px solid #d8d8d8
				.hotSpot-item-list-pic
					float:left
					width: 134px
					height: 90px
					.img
						width: 100%
				.hotSpot-item-list-txt
					float:right
					width: 52%
					height: 90px	
					.title
						font:bolder 17px "微软雅黑"
					.text
						margin-top:10px
						color:#777	
						font-size:12px
	
	.tab
		width: 100%
		height: 30px
		.tab-item
			width: 20%
			height: 30px
			float:left
			text-align:center		
			.tab-link
				display:block
				width: 100%
				color:#1c2635
				font:16px/30px "微软雅黑"	
				
	.copying
		width: 100%
		height: 50px
		background:#f6f6f6
		line-height: 50px
		text-align:center
		font-size:12px
		color:#ccc							
</style>


