<template>
	<div class="attention">
			<h1 class="attention-title"><span @click="back" style="font:bolder 20px simsun"><img src="../../common/images/back.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>   即将上映({{aboutToShowList.length}}部)</h1>
			<ul class="attention-list">
				<li v-for="item in aboutToShowList" class="attention-item" >
				    <div class="attention-move" >
				    	<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.image" width="98" height="138">
				            	<span>{{item.rYear}}</span>
				    	</a>
				        <div class="text">
				        	<p class="name">{{item.title}}<span>({{item.releaseDate}})</span></p>
				        	<p class="type"><span>{{item.wantedCount}}</span> 人想看 - {{item.type}}</p>
				        	<p class="director">导演   :   {{item.director}}</p>
				        	<p class="actor">演员:{{item.actor1}} , {{item.actor2}}</p>
						    <a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'" class="trailer">预告片</a>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!aboutToShowList.length">
        		<loading></loading>
      	</div>
		
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			aboutToShowList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getAboutToShow();
	    },
	    methods:{
	      	_getAboutToShow(){
	      		axios.get("http://120.78.84.152:8080/api/getAboutToShow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.aboutToShowList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.attention{
	position: relative;
	top: 88px;
   	padding:0 13px 0  18px
}
.attention-title{
	width: 100%
	height: 30px
	font:bolder 20px/30px "微软雅黑"
	position:relative
}
.attention-move{
    height: 150px;
    margin:5px 0 10px;
    padding:8px 0 5px;
    border: 1px dashed #bbb;
}
.attention-move a{
	float: left;
	margin-left: 5px;
	display: block;
	width: 98px;
	height: 150px;
	font-size: 14px;
} 
.attention-move a img,.attention-move a span{
	float: left;
}     			
.attention-move .text{
	float:right
	width: 63%;
	height: 150px;
	position: relative;
}   			
.attention-move .text .name{
	font:bolder 18px "微软雅黑"
	color:#1c2635
}    
.attention-move .text .name span{
	font-size: 12px;
	color: #666;
}
.attention-move .text .type{
	margin-top: 5px;
	font-size: 12px;
}
.attention-move .text .type span{
	color: #ff8600;
}
.attention-move .text .director{
	margin-top: 8px;
	font-size: 12px;
}
.attention-move .text .actor{
	margin-top: 5px;
	font-size: 12px;
}
.attention-move .text .trailer{
	position: absolute;
	bottom: 5px;
	right: 10px;
	width: 96px;
	height: 36px;
	line-height: 36px;
	text-align: center;
	border-radius:18px;
	border: 1px solid #659c0d;
	color: #659c0d;
}
.loading-container{
	position: relative
	left:50%
	top:150px
	transform: translateX(-50%)
}
	        
</style>


