<template>
  <div class="loading">
    <img width="137" height="165" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1510842820787&di=22b375387af3c8ae80b0c5f372db3487&imgtype=0&src=http%3A%2F%2Fimg.zcool.cn%2Fcommunity%2F01ba685651305a32f87512f636c134.gif">
    
    <p class="desc">{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type: String,
        default: '正在载入...'
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .loading
    width: 100%
    text-align: center
    .desc
      line-height: 20px
      font-size: 12px
      color: rgba(255, 255, 255, 0.5)
</style>



