<template>
	<div class="shop">
		 <div class="slider-wrapper" v-if="shopList.length">
          <slider>
            <div v-for="item in shopList">
              <a :href="item.url">
                <img :src="item.image" style="width:100%">
              </a>
            </div>
          </slider>
        </div>
		<div class="loading-container" v-show="!shopList.length">
        	<loading></loading>
      	</div>
	</div>
</template>

<script>
	import Slider from "base/slider/slider"
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			shopList:[]
    		}
    	},
	    components:{
	    	Loading,
	    	Slider
	    },
	    created(){
	      	this._getShop();
	    },
	    methods:{
	      	_getShop(){
	      		axios.get("http://120.78.84.152:8080/api/getShop",{}).then((res)=>{
	      			console.log(res.data)
	      			this.shopList = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">
.shop{
	position: relative;
	top:88px;
	width: 100%;
}
.slider-wrapper{
	position: relative
    width: 100%
    overflow: hidden
}
        
.loading-container{
	position: absolute
	left:50%
	top:150px
	transform: translateX(-50%)
}
</style>


