import Vue from 'vue'
import VueRouter from 'vue-router'
import HomePage from "components/home-page/home-page"
import Ticket from "components/ticket/ticket"
import Shop from "components/shop/shop"
import Find from "components/find/find"
import HotMore from "components/hot-more/hot-more"
import AboutToShowMore from "components/aboutToShow-more/aboutToShow-more"
import Attention from "components/attention/attention"
import FindNewsList from "components/findnewsList/findnewsList"
import FindTrailerList from "components/findTrailerList/findTrailerList"
import Login from "components/login/login"
import Zhuce from "components/zhuce/zhuce"
import User from "components/user/user"
import Error from "components/zhuce/error"
Vue.use(VueRouter);
export default new VueRouter({
  routes:[
    {
      path:'/',
      redirect:"/home-page"
    },{
      path:"/home-page",
      component:HomePage
    },{
      path:"/ticket",
      component:Ticket
    },{
      path:"/shop",
      component:Shop
    },{
      path:"/find",
      component:Find
    },{
      path:"/hot-more",
      component:HotMore
    },{
    	path:"/aboutToShow-more",
    	component:AboutToShowMore
    },{
    	path:"/attention",
    	component:Attention
    },{
    	path:"/findNewsList",
    	component:FindNewsList
    },{
    	path:"/findTrailerList",
    	component:FindTrailerList
    },{
    	path:"/login",
    	component:Login
    },{
    	path:"/zhuce",
    	component:Zhuce
    },{
    	path:"/user",
    	component:User
    },{
    	path:"/error",
    	component:Error
    }
    
    
    
  ]
})



