<template>
	<div class="user">
		<div class="personal">
			<span class="head-portrait">
				<img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1511937025535&di=97133549c26421eca9f12ed5f1fed27d&imgtype=0&src=http%3A%2F%2Ff.hiphotos.baidu.com%2Fimage%2Fpic%2Fitem%2F962bd40735fae6cd7613528b05b30f2443a70fb0.jpg"/>
			</span>
			<div class="msg">
				<span class="username">哈二二二二二二二</span>
				<span class="balance">余额：￥0</span>
			</div>		
		</div>
		<div class="shoptool">
			<ul>
				<li>
					<i class="car"><img src="../../common/images/i_cate.png"/></i>
					<span>购物车</span>
				</li>
				<li>
					<i class="movies"><img src="../../common/images/i_movie.png"/></i>
					<span>电影票订单</span>
				</li>
				<li>
					<i class="shop"><img src="../../common/images/i_shop.png"/></i>
					<span>商品订单</span>
				</li>
			</ul>
		</div>
		<div class="menu">
			<ul>
				<li>
					<i><img src="../../common/images/my_card.png"/></i>
					<span class="title">我的商品优惠卷</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
				<li>
					<i><img src="../../common/images/my_radio.png"/></i>
					<span class="title">我的活动</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
				<li>
					<i><img src="../../common/images/my_movie.png"/></i>
					<span class="title">我的电影</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
				<li>
					<i><img src="../../common/images/my_fav.png"/></i>
					<span class="title">我的收藏</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
			</ul>
		</div>
		<div class="menu">
			<ul>
				<li style="padding-left: 0;">
					<span class="title">意见反馈</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
				<li style="padding-left: 0;">
					<span class="title">商城使用帮助</span>
					<span class="arrow"><img src="../../common/images/arrow.png"/></span>
				</li>
			</ul>
		</div>
		<div class="btn">
			<button @click="back">退出登录</button>
		</div>
		<div class="copying">
			<p>
				<span>Copyright </span>
				<span> HiMovies.com Inc. All rights reserved.</span>
			</p>
		</div>
	</div>
</template>

<script>
export default {
	methods:{
	    back(){
         	this.$router.back()
       }
    }
}	
</script>	

<style>
.user{
	padding-top: 88px;
}
.personal{
	height: 120px;
	padding-top: 20px;
	box-shadow: 0px 1px 10px #ccc;
	box-sizing: border-box;
}
.head-portrait{
	float: left;
	width: 50%;
	height: 100%;
	text-align: center;
}
.head-portrait img{
	width: 80px;
	height: 80px;
	border-radius: 100%;
	
}
.msg{
	float:left;
	padding-top: 20px;
	width: 50%;
	height: 100%;
	box-sizing: border-box;
}
.msg span{
	display: block;
	margin-top: 5px;
	color: #666;	
	font:14px "微软雅黑";
}
.msg span:last-child{
	font-size: 12px;
}
.shoptool{
	margin-top: 10px;
	padding-top:10px ;
	height: 80px;
	box-shadow: 0px 1px 10px #ccc;
}
.shoptool ul{
	width: 100%;
}
.shoptool li{
	float: left;
	width: 33%;
	text-align: center;
	position: relative;
}
.shoptool li i{
	width: 100%;
	display: block;
	width: 48px;
	height: 48px;
}
.shoptool li span{
	display: block;
	padding-top: 55px;
	color: #666;
}
.shoptool li i img{
	transform: translateY(30%);
}
.car{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	background: #ffb059;
	border-radius:100%;
}
.movies{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	background:#78bdfd;
	border-radius:100%;
}
.shop{
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	background:#ff815a;
	border-radius:100%;
}
.menu{
	margin-top: 10px;
	padding: 5px 15px 0;
	box-shadow: 0px 1px 10px #ccc;	
}
.menu ul{
	width: 100%;
}
.menu li{
	position: relative;
	padding-left:30px ;
	box-sizing: border-box;
	width: 100%;
	height: 35px;
	font: 14px/35px "微软雅黑";
	color: #666;
	border-bottom: 1px solid #F1F1F1;
}
.menu li:last-child{
	border-bottom: none !important;
}
.menu li i{
	position: absolute;
	top: 3px;
	left: 3px;
	width: 20px;
	height: 20px;
}
.menu li i img{
	width: 100%;
}
.arrow{
	width: 20px;
	height: 20px;
	position: absolute;
	top: 3px;
	right: 2px;
}
.arrow img{
	width: 100%;
}			
.copying{
	height: 50px;	
	background:#f6f6f6;	
	line-height: 50px;	
	text-align:center;	
	font-size:12px;
	color:#ccc;		
}
.btn{
	margin: 20px 0;
	padding: 0 15px;
}
button{
	width: 100%;
	border: none;
	height: 35px;
	background: #ff6302;
	color: #fff;
	border-radius: 35px;
	outline: none;
}
</style>


