<template>
	<div class="findTrailerList">
			<h1 class="findTrailerList-title"><span @click="back" style="font:bolder 20px simsun"><img src="../../common/images/back.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>   预告片 </h1>
			<ul class="findTrailerList-list">
				<li v-for="item in findTrailerList" class="findTrailerList-item" >
				    <div class="findTrailerList-move" >
				    	<a :href="item.hightUrl">				     
				            	<img v-lazy="item.coverImg" width="150" height="90">
				    	</a>
				        <div class="text">
				        	<p class="title1">{{item.movieName}}</p>
				        	<p class="title2">{{item.summary}}</p>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!findTrailerList.length">
        		<loading></loading>
      	</div>
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			findTrailerList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getFindTrailerList();
	    },
	    methods:{
	      	_getFindTrailerList(){
	      		axios.get("http://120.78.84.152:8080/api/getFindTrailerList",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findTrailerList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.findTrailerList{
	position: relative;
	top: 88px;
   	padding:0 13px 0  18px
}
.findTrailerList-title{
	width: 100%
	height: 30px
	font:bolder 20px/30px "微软雅黑"
	position:relative
}
.findTrailerList-move{
    height: 100px;
    margin:5px 0 10px;
    padding:8px 0 5px;
    border-bottom: 1px solid #bbb;
}
.findTrailerList-move a{
	float: left;
	margin-left: 5px;
	display: block;
	width: 98px;
	height: 100px;
	font-size: 14px;
} 
.findTrailerList-move a img,.findTrailerList-move a span{
	float: left;
}     			
.findTrailerList-move .text{
	float:right
	width: 46%;
	height: 100px;
	position: relative;
}   			
.findTrailerList-move .text .title1{
	font:bolder 16px "微软雅黑"
	color:#1c2635
}  
.findTrailerList-move .text .title2{
	font-size: 12px;
	margin-top: 10px;
	color:#666
}    
.loading-container{
	position: relative
	left:50%
	top:150px
	transform: translateX(-50%)
}
	        
</style>


