<template>
	<div class="find">
		<ul class="find-list">
			<li class="findList-item">
				<h1 class="list-title">新闻
			        <router-link tag="div" class="list-item" to="/findNewsList">
				       	<span class="more-link"><img src="../../common/images/go.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			    </h1>
			   
			</li>
			<li class="findList-item">
				<h1 class="list-title">预告片
			        <router-link tag="div" class="list-item" to="/findTrailerList">
				       	<span class="more-link"><img src="../../common/images/go.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			    </h1>
			    
			</li>
			<li class="findList-item">
				<h1 class="list-title">排名
			        <router-link tag="div" class="list-item" to="/new-more">
				       	<span class="more-link"><img src="../../common/images/go.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			    </h1>
			    
			</li>
			<li class="findList-item">
				<h1 class="list-title">影评
			        <router-link tag="div" class="list-item" to="/new-more">
				       	<span class="more-link"><img src="../../common/images/go.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>
				        <keep-alive>
          					<router-view></router-view>
        				</keep-alive>
				    </router-link>
			   </h1>
			</li>
		</ul>
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			findNewsList:[],
    			findTrailerList:[],
    			findTopListList:[],
    			findReviewList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getFindNews();
	      	this._getFindTrailer();
	      	this._getFindTopList();
	      	this._getFindReview();
	    },
	    methods:{
	      	_getFindNews(){
	      		axios.get("http://120.78.84.152:8080/api/getFindNews",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findNewsList = res.data;
	      		})
	      	},
	      	_getFindTrailer(){
	      		axios.get("http://120.78.84.152:8080/api/getFindTrailer",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findTrailerList = res.data;
	      		})
	      	},
	      	_getFindTopList(){
	      		axios.get("http://120.78.84.152:8080/api/getFindTopList",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findTopListList = res.data;
	      		})
	      	},
	      	_getFindReview(){
	      		axios.get("http://120.78.84.152:8080/api/getFindReview",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findReviewList = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">
.find{
	position: relative;
	top:88px;
	width: 100%;
}
.loading-container{
	position: absolute;
	z-index: -1;
	left:50%
	top:320px
	transform: translateX(-50%)
}
.findList-item{
	position: relative;
}
.list-title{
	height: 45px
	font:bolder 18px/40px "微软雅黑"
	position:relative;
	padding:0 10px;
	/*z-index: 1;*/
}				
.list-item{
	position:absolute
	top:0px
	right:5px;
	z-index: 1;	
}
.more-link{
	font:bolder 16px/40px "宋体";
	z-index: 1;	
}
.text{
	position: absolute;
	width: 100%;
	background: rgba(0,0,0,.8);
	bottom:0;
	color: #fff;
	text-align: center;
	font:bolder 16px/30px "微软雅黑"
}					        
</style>


