<template>
	<div class="ticket">
		<ul class="ticket-more-list">
			<li v-for="item in ticketList" class="ticket-more-item" >
				<!--<a :href="'http://m.mtime.cn/#!/theater/365/'+item.cinemaId+'/date/20171116/'">-->
					<h1 class="cinema">{{item[cinemaName]}}</h1>
					<p class="address">{{item[address]}}</p>
					<!--<ul>
						<li v-html="item.feature.has3D?'http://static1.mtime.cn/html5/20171113115608/images/2014/is_cine_05.png':''"></li>
					</ul>-->
				<!--</a>-->
			</li>
		</ul>
		<div class="loading-container" v-show="!ticketList.length">
        	<loading></loading>
      	</div>
	</div>
</template>

<script>
	import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			ticketList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getTicket();
	    },
	    methods:{
	      	_getTicket(){
	      		axios.get("http://120.78.84.152:8080/api/getTicket",{}).then((res)=>{
	      			console.log(res.data)
//	      			this.ticketList = res.data[0];
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">
.ticket{
	position:relative;
	top: 88px;
	padding:0 13px 0  18px
}
.ticket-more-list{
	width: 100%
}
.loading-container{
	position: absolute
	left:50%
	top:150px
	transform: translateX(-50%)
}
</style>


