<template>
	<div class="findNewsList">
			<h1 class="findNewsList-title"><span @click="back" style="font:bolder 20px simsun"><img src="../../common/images/back.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>   新闻  ({{findNewsList.length}}条)</h1>
			<ul class="findNewsList-list">
				<li v-for="item in findNewsList" class="findNewsList-item" >
				    <div class="findNewsList-move" >
				    	<a :href="'http://m.mtime.cn/#!/news/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.image" width="90" height="90">
				    	</a>
				        <div class="text">
				        	<p class="title1">{{item.title}}</p>
				        	<p class="title2">{{item.title2}}</p>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!findNewsList.length">
        		<loading></loading>
      	</div>
		
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			findNewsList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getfindNewsList();
	    },
	    methods:{
	      	_getfindNewsList(){
	      		axios.get("http://120.78.84.152:8080/api/getfindNewsList",{}).then((res)=>{
	      			console.log(res.data)
	      			this.findNewsList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.findNewsList{
	position: relative;
	top: 88px;
   	padding:0 13px 0  18px
}
.findNewsList-title{
	width: 100%
	height: 30px
	font:bolder 20px/30px "微软雅黑"
	position:relative
}
.findNewsList-move{
    height: 100px;
    margin:5px 0 10px;
    padding:8px 0 5px;
    border-bottom: 1px solid #bbb;
}
.findNewsList-move a{
	float: left;
	margin-left: 5px;
	display: block;
	width: 98px;
	height: 100px;
	font-size: 14px;
} 
.findNewsList-move a img,.findNewsList-move a span{
	float: left;
}     			
.findNewsList-move .text{
	float:right
	width: 64%;
	height: 100px;
	position: relative;
}   			
.findNewsList-move .text .title1{
	font:bolder 16px "微软雅黑"
	color:#1c2635
}  
.findNewsList-move .text .title2{
	font-size: 12px;
	margin-top: 10px;
	color:#666
}    
.loading-container{
	position: relative
	left:50%
	top:150px
	transform: translateX(-50%)
}
	        
</style>


