<template>
	<div class="hot-more">
			<ul class="hot-more-list">
				<li v-for="item in hotList" class="hot-more-item" >
				    <div class="hot-move" >
				    	<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.img" width="98" height="138">
				    	</a>
				        <div class="text">
				        	<p class="name" v-html="item.t" ></p>
				        	<p class="introduce">{{item.commonSpecial}}</p>
				        	<ul class="ms">
				        		<li><span v-html="item.versions[0]?item.versions[0]['version']:'2D/3D'"></span></li>
				        		<li><span v-html="item.versions[2]?item.versions[2]['version']:'中国巨幕'"></span></li>
				        	</ul>
				        	<p class="status">{{item.cC}}家影院上映{{item.NearestShowtimeCount}}场</p>
				        	<router-link tag="div" class="purchase-item" to="/purchase">
						       	<span class="purchase-link">购买</span>
						        <keep-alive>
		          					<router-view></router-view>
		        				</keep-alive>
						    </router-link>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!hotList.length">
        		<loading></loading>
      		</div>
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			hotList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getHot();
	    },
	    methods:{
	      	_getHot(){
	      		axios.get("http://120.78.84.152:8080/api/getHot",{}).then((res)=>{
	      			console.log(res.data)
	      			this.hotList = res.data;
	      		})
	      	}
	    }
    }
</script>

<style scoped lang="stylus">
.hot-more{
	position: relative;
	top:88px
   	padding:0 13px 0  18px
}

.hot-more-list{
	width: 100%
}
.hot-move{
	width: 100%
    height: 140px
    padding:8px 0 10px
    border-bottom:1px solid #f1f1f1
}
.hot-move a{
	float: left
}     			
.hot-move .text{
	float:right
	width: 65%;
	height: 140px;
	position: relative;
}   			
.hot-move .text .name{
	font:bolder 18px "微软雅黑"
	color:#1c2635
}    
.hot-move .text .introduce{
	color: #659d0e
	font-size: 14px
} 			
.hot-move .text .ms{
	float: left
	margin-top:5px
	width: 100%
}     		
.hot-move .text .ms li{
	float:left;
	margin: 2px;
	color: #659d0e
	border:1px solid #659d0e;
	font-size:14px
}	
.hot-move .text .status{
	float: left;
	margin-top: 10px;
	color:#999;
	font-size: 13px;
}
.hot-move .text .purchase-item{
	position: absolute;
	bottom: 0;
	right:0;
	width: 90px;
	height: 30px;
	line-height: 30px;
	text-align: center;
	border-radius:15px;
	background: #ff8600;
	color: #fff;
	font-size: 14px;
}
.loading-container{
	position: absolute
	left:50%
	top:150px
	transform: translateX(-50%)
}
	        
</style>


