<template>
	<div class="aboutToShow-more">
			<h1 class="follow-title"><span @click="back" ><img src="../../common/images/back.png" style="width: 20px; height: 20px; position: relative;top: 3px;"/></span>   最受关注({{followList.length}}部)
			    <router-link tag="div" class="attention-item" to="/attention">
					<span class="attention-link"><i>more</i><img src="../../common/images/go.png" style="width: 10px; height: 10px; position: relative;top: 1px;" /></span>
					<keep-alive>
          				<router-view></router-view>
        			</keep-alive>
				</router-link>
			</h1>
			<ul class="aboutToShow-more-list">
				<li v-for="item in followList" class="aboutToShow-more-item" >
				    <div class="aboutToShow-move" >
				    	<a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'">				     
				            	<img v-lazy="item.image" width="98" height="138">
				            	<span>{{item.rYear}}</span>
				    	</a>
				        <div class="text">
				        	<p class="name">{{item.title}}<span>({{item.releaseDate}})</span></p>
				        	<p class="type"><span>{{item.wantedCount}}</span> 人想看 - {{item.type}}</p>
				        	<p class="director">导演   :   {{item.director}}</p>
				        	<p class="actor">演员:{{item.actor1}} , {{item.actor2}}</p>
				        	<router-link tag="div" class="purchase-item" to="/purchase">
						       	<span class="purchase-link">超前预售</span>
						        <keep-alive>
		          					<router-view></router-view>
		        				</keep-alive>
						    </router-link>
						    <a :href="'http://m.mtime.cn/#!/movie/'+item.id+'/'" class="trailer">预告片</a>
				        </div>
				    </div>
				</li>
			</ul>
			<div class="loading-container" v-show="!followList.length">
        		<loading></loading>
      	</div>
		
		
	</div>
</template>

<script>
    import Loading from "base/loading/loading"
	import axios from "axios"
	export default {
    	data(){
    		return{
    			followList:[]
    		}
    	},
	    components:{
	    	Loading
	    },
	    created(){
	      	this._getFollow();
	    },
	    methods:{
	      	_getFollow(){
	      		axios.get("http://120.78.84.152:8080/api/getFollow",{}).then((res)=>{
	      			console.log(res.data)
	      			this.followList = res.data;
	      		})
	      	},
	      	back(){
         		this.$router.back()
        	}
	    }
    }
</script>

<style scoped lang="stylus">
.aboutToShow-more{
	position:relative;
	top:88px
}
.follow-title{
	width: 100%
	height: 30px
	font:bolder 20px/30px "微软雅黑"
	position:relative
}
.attention-item{
	position:absolute
	top:0px
	right:5px
}
.attention-link{
	font:bold 12px "宋体";
	color: #666;	
}
.attention-link i{
	font: 14px "微软雅黑"
}
.aboutToShow-more{
	position: relative
   	padding:0 13px 0  18px
}
.aboutToShow-more-content{
	height:100%
    overflow:hidden
}
.aboutToShow-more-list{
	width: 100%
}
.aboutToShow-move{
    height: 150px;
    margin-bottom: 15px;
    padding:8px 0 0
    /*border-top:1px dashed #bbb*/
    border-right:1px dashed #bbb
}
.aboutToShow-move a{
	float: left;
	display: block;
	width: 98px;
	height: 150px;
	font-size: 12px;
} 
.aboutToShow-move a img,.aboutToShow-move a span{
	float: left;
}     			
.aboutToShow-move .text{
	float:right
	width: 65%;
	height: 150px;
	position: relative;
}   			
.aboutToShow-move .text .name{
	font:bolder 16px "微软雅黑"
	color:#1c2635
}    
.aboutToShow-move .text .name span{
	font-size: 12px;
	color: #666;
}
.aboutToShow-move .text .type{
	margin-top: 5px;
	font-size: 12px;
}
.aboutToShow-move .text .type span{
	color: #ff8600;
}
.aboutToShow-move .text .director{
	margin-top: 10px;
	font-size: 12px;
}
.aboutToShow-move .text .actor{
	margin-top: 5px;
	font-size: 12px;
}
.aboutToShow-move .text .introduce{
	color: #659d0e
	font-size: 16px
} 			
.aboutToShow-move .text .ms{
	float: left
	margin-top:5px
	width: 100%
}     		
.aboutToShow-move .text .ms li{
	float:left;
	margin: 2px;
	color: #659d0e
	border:1px solid #659d0e;
	font-size:14px
}	
.aboutToShow-move .text .status{
	float: left;
	margin-top: 10px;
	color:#999;
	font-size: 14px;
}
.aboutToShow-move .text .purchase-item{
	position: absolute;
	bottom: 5px;
	left:0;
	width: 86px;
	height: 36px;
	line-height: 36px;
	text-align: center;
	border-radius:18px;
	background: #ff8600;
	color: #fff;
	font-size: 15px;
}
.aboutToShow-move .text .trailer{
	position: absolute;
	bottom: 5px;
	right: 10px;
	width: 86px;
	height: 36px;
	line-height: 36px;
	text-align: center;
	border-radius:18px;
	border: 1px solid #659c0d;
	color: #659c0d;
}
.loading-container{
	position: absolute
	left:50%
	top:150px
	transform: translateX(-50%)
}
	        
</style>


